.myStyle{
    border: 2px solid green;
    width: 50vw;
    margin: auto;
    border-radius:5px;
    min-width:292px;
    max-width:320px;
    padding:12px;
    background-image:url("../image/texture1.jpg");
}


.inputStyle{
    border: none;
    border-bottom: 2px solid green;
    border-radius: 2px;
    background-color: transparent;
}
.inputStyle:focus{
    outline:none;
}
.disabledColor{
    border-color:rgba(50, 49, 49, 0.942);
    opacity:0.6;
}
.heading4{
    background-image: linear-gradient(to right,red, yellow);
    font-weight:700;
    -webkit-background-clip: text;
    color:transparent;
}
.skeleton{
    display:flex;
    /* margin:auto; */
    
}


@media screen and (max-width: 500px) {
    .skeleton {
      display: flex;
      flex-direction:column;
    }
  }